body {
  color: #fafafa;
  background-color: #0c0b0b;
  margin: 0;
  font-family: Poppins, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
}

h1, h2, h3, h4, p {
  margin: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
  border: none;
  font-family: inherit;
}

input {
  font-family: inherit;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

svg {
  fill: currentColor;
}

.header {
  z-index: 2;
  background-color: rgba(0, 0, 0, 0);
  padding: 20px 0;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
}

.header.solid_header {
  background-color: #0c0b0b;
}

.header .wrapper {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header .search_form {
  width: 108px;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.header .search_form :is(input:focus, input:valid) ~ div > .label {
  display: none;
}

.header .search_form :is(input:focus, input:valid) ~ .button_label_wrap {
  left: 18.5px;
}

.header .search_form .button_label_wrap {
  justify-content: center;
  align-items: center;
  gap: 8px;
  display: flex;
  position: absolute;
}

.header .search_form .button_label_wrap .button {
  background-color: rgba(0, 0, 0, 0);
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
}

.header .search_form .button_label_wrap .button .icon {
  stroke: #fafafa;
}

.header .search_form .button_label_wrap .label {
  text-transform: uppercase;
  cursor: text;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.17;
}

.header .search_form .input {
  width: 100%;
  color: #fafafa;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid rgba(250, 250, 250, .3);
  border-radius: 1000px;
  padding: 6px 18px 6px 40px;
  font-size: 12px;
  line-height: 1.17;
}

.header .search_form .input:focus {
  border-color: #ed1d24;
  outline: none;
}

.header .search_form .input:focus + .button_label_wrap svg {
  stroke: #ed1d24;
}

